a,abbr,acronym,address,applet,article,aside,audio,b,big,blockquote,body,root,caption,center,cite,code,dd,del,details,dfn,div,dl,dt,em,embed,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,html,i,iframe,img,ins,kbd,label,legend,li,mark,menu,nav,object,ol,output,p,pre,q,ruby,s,samp,section,small,span,strike,strong,sub,summary,sup,table,tbody,td,tfoot,th,thead,time,tr,tt,u,ul,var,video{margin:0;padding:0;border:0;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:after,blockquote:before,q:after,q:before{content:'';content:none}body,html{height:100%}table{border-collapse:collapse;border-spacing:0}
/* App styles */
body {
  background-color: #212121;
  color: #ffffff;
  font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: 0.03rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                          supported by Chrome and Opera */
}

/* Header styles */
header {
  margin: 1.12rem;
  padding: 1.12rem;
}

header h2 {
  font-size: 3.75rem;
  font-weight: 300;
  letter-spacing: -0.03rem;
}
/* Hues styles */
.Hues {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  padding: 1.12rem;
}
/* Hue styles */
.Hue {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 0 1 19rem;
          flex: 0 1 19rem;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 1.12rem;
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.2),
              0px 2px 2px 0px rgba(0,0,0,0.14),
              0px 3px 1px -2px rgba(0,0,0,0.12);
  height: 100%;
}
/* Shade styles */
.Hue_Shade {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 0.87rem;
  border: 1px solid rgba(0, 0, 0, 0.12);
  margin-top: -1px;
  cursor: copy;
}

.Hue_Shade:first-child {
  margin-top: 0px;
}

.Hue_Shade--light {
  color: rgba(255, 255, 255, 0.87);
}

.Hue_Shade--dark {
  color: rgba(0, 0, 0, 0.87);
}

/* Name styles */
.Hue_Shade_Name {
  font-size: 1rem;         
}
/* Hex styles */
.Hue_Shade_Hex {
  font-family: 'Roboto Mono', 'Roboto', "Courier New", Courier, "Lucida Console", Monaco, monospace;
  font-weight: 500;
  font-size: .91rem;
  letter-spacing: 0.02rem;
}
